.voucher-code {
    padding: 3rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    font-weight: 700;
    color: #fff;
}

.voucher-code img {
    width: 12rem;
    border-radius: 0.3rem;
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.2);
}

.voucher-code .copy-code {
    cursor: pointer;
    border-radius: 0.5rem;
    font-size: 1rem;
    font-weight: 100;
    border: none;
    line-height: 1;
    font-weight: bold;
    transition: all 0.2s ease-in-out;
}

.voucher-code .copy-code:focus {
    outline: none;
}

.voucher-code .copy-code {
    background-color: #fff;
    color: #1b2e7a;
    padding: 0.4rem 1.2rem;
    margin: 0.7rem;
}

.voucher-code .copy-code:hover {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 1260px) {
    .voucher-code {
        flex-direction: column;
        text-align: center;
    }

    .voucher-code > * {
        width: 100%;
        text-align: center;
    }
}

@media screen and (max-width: 940px) {
    .voucher-code {
        padding-top: 0;
    }
}

@media screen and (max-width: 720px) {
    .voucher-code {
        width: 100%;
    }
}
