@import "~@fortawesome/fontawesome-free/css/all.min.css";

html,
body {
    padding: 0;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell,
    Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

a {
    color: inherit;
    text-decoration: none;
}

* {
    box-sizing: border-box;
}

body * {
    box-sizing: border-box;
    font-family: sans-serif;
}

body {
    margin: 0;
}

.snackbar-container {
    transition: all 500ms ease;
    transition-property: top, right, bottom, left, opacity;
    font-family: Roboto, sans-serif;
    font-size: 14px;
    min-height: 14px;
    background-color: #070b0e;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    line-height: 22px;
    padding: 18px 24px;
    bottom: -100px;
    top: -100px;
    opacity: 0;
    z-index: 9999;
}

.snackbar-container .action {
    background: inherit;
    display: inline-block;
    border: none;
    font-size: inherit;
    text-transform: uppercase;
    color: #4caf50;
    margin: 0 0 0 24px;
    padding: 0;
    min-width: min-content;
    cursor: pointer;
}

@media (min-width: 640px) {
    .snackbar-container {
        min-width: 288px;
        max-width: 568px;
        display: inline-flex;
        border-radius: 2px;
        margin: 24px;
    }
}

@media (max-width: 640px) {
    .snackbar-container {
        left: 0;
        right: 0;
        width: 100%;
    }
}

.snackbar-pos.bottom-center {
    top: auto !important;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
}

.snackbar-pos.bottom-left {
    top: auto !important;
    bottom: 0;
    left: 0;
}

.snackbar-pos.bottom-right {
    top: auto !important;
    bottom: 0;
    right: 0;
}

.snackbar-pos.top-left {
    bottom: auto !important;
    top: 0;
    left: 0;
}

.snackbar-pos.top-center {
    bottom: auto !important;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
}

.snackbar-pos.top-right {
    bottom: auto !important;
    top: 0;
    right: 0;
}

@media (max-width: 640px) {
    .snackbar-pos.bottom-center,
    .snackbar-pos.top-center {
        left: 0;
        transform: none;
    }
}


.ar {
    direction: rtl;
}

.influencer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    min-height: 20rem;
    padding: 0 7rem;
}

.influencer > * {
    width: 50%;
}

.danger {
    color: #d75548;
}

.block {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    min-height: 20rem;
    padding: 0 7rem;
}

.blockedImage {
    width: auto;
    max-width: 15rem;
}

.blockText {
    color: #f4f4f4;
    font-size: 1rem;
}

@media screen and (max-width: 1260px) {
    .influencer {
        padding: 1rem 1rem;
    }

    .blockedImage {
        width: 250px;
    }
}

@media screen and (max-width: 940px) {
    .influencer {
        padding-top: 0;
        flex-direction: column;
        align-items: center;
    }

    .influencer > * {
        width: 85%;
    }

    .influencer > div:first-child > p:last-child {
        position: relative;
        padding-bottom: 1.5rem;
    }

    .influencer > div:first-child > p:last-child::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 70%;
        transform: translateX(20%);
        border-bottom: 1px solid #f4f4f425;
    }
}

@media screen and (max-width: 720px) {
    .influencer > * {
        width: 100%;
    }

    .influencer > div:first-child > p:last-child::after {
        width: 90%;
        transform: translateX(5%);
    }
}

