.congratulations {
    padding: 0.5rem;
    color: #fff;
}

.congratulations h2 {
    font-size: 2.5rem;
    font-weight: bold;
}

.congratulations h3 {
    font-size: 1.5rem;
    font-weight: bold;
    padding-bottom: 2rem;
    color: #f4f4f4;
}

.congratulations p {
    font-size: 1rem;
    color: #f4f4f4;
}

.app-links {
    display: inline-flex;
    flex-direction: row;
}

.download-btn {
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-width: 7rem;
    margin: 0.5rem;
    padding: .3rem .5rem;
    cursor: pointer;
    font-weight: bold;
    border: 1px solid #f4f4f4;
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
}

.download-btn:hover {
    background: #f4f4f4;
    color: #152ca8;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 1260px) {
    .congratulations {
        text-align: center;
    }
}

@media screen and (max-width: 768px){
    .app-links {
        flex-direction: column;
    }
}
