.cards-container {
    margin-top: 4rem;
    padding: 4rem 7rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
    width: 100%;
    background-color: #f4f4f4;
}

.card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: white;
    padding: 1.5rem;
    border-radius: 0.5rem;
    box-shadow: 0 0.3rem 0.4rem rgba(0, 0, 0, 0.2);
    transition: 0.2s all ease-in-out;
}

.card:hover {
    transform: scale(1.02) translateY(-0.5rem);
    box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.2);
}

.card img {
    max-width: 5rem;
}

.card > div {
    padding-left: 10px;
    width: 85%;
}

.card > div h3 {
    font-size: 1.2rem;
    margin: 0;
}

.card > div p {
    color: gray;
    line-height: 1.4;
    margin-bottom: 0;
    font-size: 1rem;
}

@media screen and (max-width: 1260px) {
    .card {
        max-width: 100%;
        min-width: 100%;
    }
}

@media screen and (max-width: 1080px) {
    .cards-container {
        padding: 4rem 4rem;
    }
}

@media screen and (max-width: 940px) {
    .cards-container {
        margin-top: 0;
        padding: 4rem 4rem;
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 720px) {
    .cards-container {
        padding: 4rem 2rem;
    }
}
