.header {
    position: relative;
    border-bottom: 1px solid rgb(136, 136, 136);
    display: flex;
    width: 100%;
    padding: 0 7rem;
    color: white;
    justify-content: space-between;
    align-items: center;
}

.header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 32rem;
    background-color: #f4f4f4;
    z-index: -2;
}

.header::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 28rem;
    transform: skewY(-4deg);
    transform-origin: left;
    background: linear-gradient(to right, #16095e, #152ca8);
    z-index: -1;
}

.header img {
    max-width: 9rem;
}

.header .desktop-nav {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.header .desktop-nav a {
    display: inline-block;
    text-decoration: none;
    color: #fff;
    padding: 1.5rem 1rem;
    position: relative;
}

.header .desktop-nav a:hover {
    cursor: pointer;
}

.header .desktop-nav a::before {
    position: absolute;
    content: "";
    width: 0%;
    height: 0.3rem;
    bottom: 0;
    left: 0;
    background: #fff;
    transform-origin: center;
    transition: all 0.2s ease-in;
}

.header .desktop-nav a:hover::before {
    transform-origin: center;
    width: 100%;
}

.mobile-nav {
    display: none;
}

.side-nav {
    background-color: #f4f4f4;
    position: fixed;
    top: 0;
    left: 0;
    width: 40vw;
    height: 100vh;
    overflow: hidden;
    padding-top: 0.4rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 10;
    transform: translateX(-100%);
    transition: 0.5s all ease-in;
}

.side-nav.active {
    transform: translateX(0);
}

.side-nav a {
    font-weight: bold;
    padding: 1.5rem;
    border-bottom: 1px solid #b1b1b1;
    width: 100%;
    transition: 0.2s all ease-in-out;
}

.side-nav a:hover {
    border-color: #1d3b9a;
    color: #1d3b9a;
}

.side-nav a:hover:not(:first-child),
.side-nav a:focus:not(:first-child) {
    background-color: #c1c1c1;
}

.side-nav a:first-child {
    text-align: right;
    margin-bottom: 1rem;
    border-bottom: none;
}

.close-btn,
.open-menu {
    cursor: pointer;
    font-size: 1.7rem;
}

.close-btn {
    padding: 0.7rem 1.5rem;
}

@media screen and (max-width: 1260px) {
    .header {
        height: 4rem;
        padding: 0 3rem;
        direction: ltr;
    }

    .header::before {
        height: 52rem;
    }

    .header::after {
        height: 85vh;
        max-height: 35rem;
    }
}

@media screen and (max-width: 940px) {
    .header::before {
        height: 50rem;
    }

    .header::after {
        min-height: 40rem;
    }

    .desktop-nav {
        display: none !important;
    }

    .mobile-nav {
        display: block;
    }

    .side-nav {
        width: 50vw;
    }
}

@media screen and (max-width: 768px) {
    .header {
        padding: 0 1.5rem;
    }

    .header::before {
        height: 55rem;
    }

    .header::after {
        min-height: 43rem;
    }

    .side-nav {
        width: 80vw;
    }
}
